import React from 'react';
import '../src/css/style.css';


const GetNotes = (props) => { 
      
       const NotesList = props.JSONnotes.map((item)=>{
               var id_num = item.id;
               var title = item.title;
               var my_notes = item.note;
         return( <div key = {id_num} className = "cardSmall">                     
                  
                       <div className="noteName">{title}
                       <i onClick = {(event) => props.deleteMe(id_num,title,my_notes)}className="fas fa-trash"></i>
                        <i onClick = {(event) => props.editNote(id_num ,title , my_notes)}className="fas fa-edit"></i>
                        
                        </div>
                       

                        <div className = "savedNotes" id="style-10" >{my_notes}</div>

                       </div> )
               })
         return <div className ="container" >
            {NotesList}
         </div>
       }


   export default GetNotes;

   